.side-drawer {
    height: 100%;
    background-color: black;
    box-shadow: 1px 0px 7px rgba(0,0,0,0.5);
    position: fixed;
    top: 0;
    right: 0;
    width: 70%;
    max-width: 400px;
    z-index: 200;
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
    transition: 0.4s;
}

.side-drawer.open{
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

