.footer{
    height: 150px;
    background: rgb(192,192,192, .4);
}

.social-circle i {
    -webkit-transition: all 0.8s;
    -moz-transition: all 0.8s;
    -o-transition: all 0.8s;
    -ms-transition: all 0.8s;
    transition: all 0.8s;
  }
  
  
  /* footer social icons */
  ul.social-network {
    list-style: none;
    display: inline;
    margin-left:0 !important;
    padding: 0;
  }
  ul.social-network li {
    display: inline;
    margin-right: 25px;
  }

  .icon-contact{
    margin-left: auto;
    margin-right: auto;
    min-width:200px
  }
  